
.home-wrapper {
  width: 100%;
  height: 100%;
  overflow-y: auto;
}
.main-content {
  background: #f5f5f5;
  position: absolute;
  height: 1px;
  top: 0;
  left: 0;
  width: 100%;
  padding-top: 145px;
}
.main-content .recommend {
    line-height: 1.5rem;
    font-size: 1rem;
    color: green;
    margin: 15px 0 10px 0;
}
.main-content .home-banner-container {
    width: 100%;
    box-sizing: border-box;
    padding: 2px;
}
.main-content .home-banner-container .home-banner {
      width: 100%;
}
.main-content .home-banner-container .home-banner .swiper-item-img img {
        width: 100%;
}



