
.block-group {
  width: 100%;
  padding: 0;
  overflow: auto;
}
.block-group-header {
    width: 100%;
    padding: 15px;
    box-sizing: border-box;
}
.block-group-content {
    width: 100%;
    box-sizing: border-box;
}
.block-group-content .block-group-item {
      background: #fff;
      display: block;
      border: 1px solid #f6f6f6;
      border-left: none;
      width: 50%;
      float: left;
      box-sizing: border-box;
}
.block-group-content .block-group-item:first-of-type {
        border-left: 1px solid #f6f6f6;
}
.block-group-content .block-group-item label {
        display: block;
        width: 100%;
        text-align: center;
        text-indent: 15px;
        font-size: 1em;
        color: #c40000;
        line-height: 2em;
}
.block-group-content .block-group-item img {
        width: 50%;
        height: auto;
        margin-top: 25%;
        margin: auto auto;
}
.block-group-content .block-group-item p {
        line-height: 1em;
        font-size: .5em;
        color: gray;
}



